.button_custom {

  & > button {
    width: 100%;
    height: 30px;
    border: none;
    color: #ffffff;
    padding: 5px 0;
    border-radius: 2px;
    font-weight: 500;
    font-size: 16px;

    & > i {
      margin: 0 5px;
    }
  }
}

.btn {
  background-color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
  width: 230px;
  height: 47px;
  line-height: 47px;
  border-radius: 5px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.02);
  display: flex;
  padding: 0;
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: #4664ff;
    color: #fff;
    transform: scale(1.02);
  }
  & > i {
    flex: 0.2;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 18px;
    border-right: 1px solid #ebe7fb;
  }

  & > span {
    flex: 0.8;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
